CtB,aAGC,cAAe,CACf,eAAgB,CAChB,mBAAqB,CACrB,oBAAqB,CACtB,QCtCC,kBPEkB,CODlB,oBPSc,CORf,iBAGC,+BAAkD,CAClD,aPNiB,COOjB,QAAS,CACT,cPCa,COAb,iBAAkB,CACnB,uBAIC,kBPbY,COcZ,aPfiB,COgBjB,YAAa,CACb,6BAA8B,CAC9B,cPVa,COWd,8BAGC,QAAS","sourcesContent":["// __ signifies a parent child such as footer__credits,\r\n//    which signifies that this is \"credits\" contained within \"footer\".\r\n\r\n// -- signifies a modifier. So footer--large would be used to say this is a \"large\" \"footer\",\r\n//    which could be a variation on footer used for something like a home page.\r\n\r\n\r\nhtml {\r\n  font-size: 62.5%;\r\n}\r\n\r\nbody {\r\n  font-family: Helvetica, Arial, sans-serif;\r\n  font-size: $m-size;\r\n  background: $dark-blue;\r\n}\r\n\r\nbutton {\r\n  cursor: pointer;\r\n}\r\n\r\nbutton:disabled {\r\n  cursor: default;\r\n}","// Colors\r\n$off-black: #20222b;\r\n$off-white: #a5afd7;\r\n$blue: #3c4251;\r\n$light-blue: #464b5e;\r\n$dark-blue: #333745;\r\n$purple: #8357c5;\r\n\r\n// Spacing\r\n$s-size: 1.2rem;\r\n$m-size: 1.6rem;\r\n$l-size: 3.2rem;\r\n$xl-size: 4.8rem;\r\n$desktop-breakpoint: 45rem;\r\n\r\n","// Add Option Error\r\n.add-option-error {\r\n  color: $off-white;\r\n  font-style: italic;\r\n  margin: $m-size 0 0 0;\r\n  padding: 0 $m-size;\r\n}\r\n\r\n// Add Option\r\n.add-option {\r\n  display: flex;\r\n  flex-direction: column;\r\n  padding: $m-size;\r\n}\r\n\r\n.add-option__input {\r\n  background: $dark-blue;\r\n  border: none;\r\n  color: $off-white;\r\n  border-bottom: .3rem solid darken($dark-blue, 10%);\r\n  flex-grow: 1;\r\n  margin: 0 0 $s-size 0;\r\n  padding: $s-size;\r\n}\r\n\r\n@media (min-width: $desktop-breakpoint) {\r\n  .add-option {\r\n    flex-direction: row;\r\n  }\r\n\r\n  .add-option__input {\r\n    margin: 0 $s-size 0 0; // margin-right: $s-size;\r\n  }\r\n}","// Big Button\n.big-button {\n  background: $purple;\n  border: none;\n  border-bottom: .6rem solid darken($purple, 10%);\n  color: white;\n  font-weight: bold;\n  font-size: $l-size;\n  margin-bottom: $m-size;\n  padding: 2.4rem;\n  width: 100%;\n}\n\n.big-button:disabled {\n  opacity: .5;\n}\n\n@media (min-width: $desktop-breakpoint) {\n  .big-button {\n    margin-bottom: $xl-size;\n  }\n}\n\n// Button\n.button {\n  background: $purple;\n  border: none;\n  border-bottom: .3rem solid darken($purple, 10%);\n  color: white;\n  font-weight: 500;\n  padding: $s-size;\n}\n\n.button--link {\n  background: none;\n  border: none;\n  color: $off-white;\n  padding: 0;\n}\n",".container {\r\n  max-width: 60rem;\r\n  margin: 0 auto;\r\n  padding: 0 $m-size;\r\n}",".header {\r\n  background: $off-black;\r\n  color: white;\r\n  margin-bottom: $m-size;\r\n  padding: $m-size 0;\r\n}\r\n\r\n// BEM block element modifier\r\n.header__title {\r\n  font-size: $l-size;\r\n  margin: 0;\r\n}\r\n\r\n.header__subtitle {\r\n  color: $off-white;\r\n  font-size: $m-size;\r\n  font-weight: 500;\r\n  margin: 0;\r\n}\r\n\r\n@media (min-width: $desktop-breakpoint) {\r\n  .header {\r\n    margin-bottom: $xl-size;\r\n  }\r\n}",".option {\r\n  border-bottom: 1px solid lighten($light-blue, 10%);\r\n  display: flex;\r\n  justify-content: space-between;\r\n  padding: $l-size $m-size;\r\n}\r\n\r\n.option__text {\r\n  color: white;\r\n  font-weight: 500;\r\n  font-size: 2rem;\r\n  margin: 0;\r\n  word-break: break-all;\r\n}",".ReactModalPortal>div {\r\n  opacity: 0;\r\n}\r\n\r\n.ReactModalPortal .ReactModal__Overlay {\r\n  // or I can put opacity: 0 here and delete the above rule\r\n  transition: opacity 200ms ease-in-out;\r\n  display: flex;\r\n  justify-content: center;\r\n  align-items: center;\r\n}\r\n\r\n.ReactModalPortal .ReactModal__Overlay--after-open {\r\n  opacity: 1;\r\n}\r\n\r\n.ReactModalPortal .ReactModal__Overlay--before-close {\r\n  opacity: 0;\r\n}\r\n\r\n.modal {\r\n  background: $light-blue;\r\n  color: white;\r\n  max-width: 30rem;\r\n  outline: $light-blue;\r\n  padding: $l-size;\r\n  text-align: center;\r\n  max-height: calc(100vh - 150px);\r\n  overflow-y: auto; \r\n}\r\n\r\n.modal__title {\r\n  margin: 0 0 $m-size 0;\r\n}\r\n\r\n.modal__body {\r\n  font-size: 2rem;\r\n  font-weight: 300;\r\n  margin: 0 0 $l-size 0;\r\n  word-break: break-all;\r\n}","// Widget\n.widget {\n  background: $light-blue;\n  margin-bottom: $xl-size;\n}\n\n.widget__message {\n  border-bottom: 1px solid lighten($light-blue, 10%);\n  color: $off-white;\n  margin: 0;\n  padding: $l-size;\n  text-align: center;\n}\n\n// Widget Header\n.widget__widget-header {\n  background: $blue;\n  color: $off-white;\n  display: flex;\n  justify-content: space-between;\n  padding: $m-size;\n}\n\n.widget__widget-header__title {\n  margin: 0;\n}\n\n\n// .widget-header {\n//   background-color: $dark-blue-lighter;\n//   color: $off-white;\n//   display: flex;\n//   justify-content: space-between;\n//   padding: $m-size;\n  \n//   &__title {\n//    margin: 0;\n//   }\n//  }"],"sourceRoot":""}]);
